	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/css-selection-styles/index.md absPath of file >>> MarkdownRemark","html":"<p>Did you know you could customize the selection colours of html pages with CSS?  I didn't - until now.  Turns out, there's a <a href=\"http://www.w3.org/TR/css3-selectors/#UIfragments\" title=\"Open link in a new window\" target=\"_blank\">CSS3 pseudo-element called ::selection</a>.  Naturally, this isn't yet a technique supported by browsers like Internet Explorer, but it is supported in [albeit with different syntax] by Firefox and Safari.  Interesting, eh?  Check an article on <a href=\"http://www.quirksmode.org/css/selection.html\" title=\"Open link in a new window\" target=\"_blank\">CSS Selection Styles from Quirksmode</a>, and have a look at some of the creative effects you can get with it <a href=\"http://metaatem.net/selcolor.php\" title=\"Open link in a new window\" target=\"_blank\">here</a>.</p>","frontmatter":{"title":"CSS Selection Styles?","date":"February 24, 2008","tags":["css","css3"]}}},"pathContext":{}}

